.sd-tooltips {
  .intro_text {
    position: absolute;
    z-index: -1;
  }
  img {
    display: block;
  }
}
.tooltips-section {
  position: relative;
}

.tooltips-figure {
  margin: 0;
}

.tooltips-img {
  display: block;
  width: 100%;
}

.tooltips-list {
  padding: 0 0 0 32px;
  list-style: decimal;
}

.tooltip-item {
  box-sizing: border-box;
  padding: 8px 12px;
}

.tooltip-button {
  background: transparent;
  width: 100%;
  border: 0;
  padding: 0;
  text-align: left;
  z-index: 1;
}

.tooltip-button:focus {
  outline: none;
}

.tooltip-index {
  border-radius: 50%;
  text-align: center;
  position: absolute;
  padding: 15px;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 48px;
  height: 48px;
  transition: var(--transition);
  span {
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: rgba(255,255,255,0.8);
    position: relative;
    &:after {
      content: " ";
      left: 3px;
      right: 3px;
      top: 3px;
      bottom: 3px;
      position: absolute;
      display: block;
      border-radius: 50%;
      border: 1px solid rgba(var(--color-red-rgb),0.0);
      transition: var(--transition);
    }
  }
}
.tooltip-button[aria-expanded="true"] .tooltip-index, .tooltip-button:focus .tooltip-index, .tooltip-button:hover .tooltip-index {
  //padding: 12px;
  span {
    &:after {
      border: 1px solid var(--color-red);
    }
  }
}
.tooltip-button[aria-expanded="true"] .tooltip-index, .tooltip-button:focus .tooltip-index {
  //padding: 12px;
  span {
    &:after {
      border: 1px solid var(--color-red);
    }
  }
}

.tooltip-overlay {
  background: #fff;
  opacity: 1;
  overflow: hidden;
  will-change: height;
}

.tooltip-header {
  display: none;
}

.tooltip-item .tooltip-overlay:empty {
  animation: none;
}


[aria-expanded="true"] ~ .tooltip-overlay {
  animation: tooltip-expand 200ms both cubic-bezier(0.4, 0, 0.2, 1);
}

[aria-expanded="false"] ~ .tooltip-overlay {
  animation: tooltip-collapse 180ms both cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes tooltip-expand {
  0% { height: var(--start-h) }
  100% { height: var(--end-h) }
}

@keyframes tooltip-collapse {
  0% { height: var(--start-h) }
  100% { height: var(--end-h) }
}

@media (prefers-reduced-motion: reduce) {
  .tooltip-overlay {
    animation-duration: 0s!important;
  }
}

