// Tooltip Products
.tooltip-item {
  .tip_product {
    font-family: var(--font-heading-family);
    .vendor, .price {
      font-size: 87.5%;
      color: var(--color-dark);
    }
    .title {
      svg {
        transition: var(--transition);
        margin-left: 4px;
      }
      &:hover {
        svg {
          transform: translateX(4px);
        }
      }
    }
  }
}